import React, { Component } from 'react'
import './App.css';
import {HashRouter as Router,Switch,Route} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.css'
import Help from './components/Help';
import Homepage from './components/Homepage';
import Main from './components/Main';

export default class App extends Component {
  state ={
    service:'100 - Police',
    user:{},
  }

  
  submit=(user)=>{
    this.setState({user:user})  
  }
  chooseService=(e)=>{
    this.setState({service:e})
  }
  
  render() {
    let path = `/main/${this.state.user.userName}`
    let path1 = `/help/${this.state.service}`
    return (
      <div>
        <Router>
          <Switch>
            <Route exact path ='/' component={()=>{return <Homepage submit={this.submit}/>}}></Route>
            <Route exact path ={path} component={()=>{return <Main user={this.state.user} service={this.state.service} chooseService={this.chooseService}/>}}/>
            <Route exact path={path1} component={()=>{return <Help user={this.state.user} service={this.state.service}/>}}/>
          </Switch>
          <h1 style={{position:'relative',left:'1200px',top:'300px'}}>HelpMe!</h1>
        </Router>          
      </div>
     
    )
  }
}

