import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class Sign extends Component {
    state={
        userName:'',
        userPass:'',
        vailduserName:true,
        vailduserPass:true,
    }

    handleuserName = (e) =>{
        if(e.target.value.length >= 4 ){
            if(e.target.value.match(/^[a-z,A-Z]+$/)){
                this.setState({userName:e.target.value , vailduserName:false})
            }else{
                this.setState({vailduserName:true})
            }
        }else{
            this.setState({vailduserName:true})
        }  

    }

    handleuserPassword = (e) =>{
        if(e.target.value.length === 8 ){
            if(e.target.value.match(/^[0-9a-zA-Z]+$/)){
                this.setState({userPass:e.target.value , vailduserPass:false})
            }else{
                this.setState({vailduserPass:true})
            }
        }else{
            this.setState({vailduserPass:true})
        }  
    }

    vailedUser=()=>{
        if(!this.state.vailduserName && !this.state.vailduserPass){
            this.props.submit({userName:this.state.userName , userPass:this.state.userPass})
        }
    }




    render() {
        let vailduserName=''
        let vailduserPass=""
        let path = `/main/${this.state.userName}`
        if(this.state.vailduserName){vailduserName=<p style={{color:'red',fontSize:'25px'}}> must be at least 4 chars with no numbres </p>}
        if(this.state.vailduserPass){vailduserPass=<p style={{color:'red',fontSize:'25px'}}> must be 8 chars with numbers </p>}

        return (
        <div className='App'>
            <div style={{backgroundColor:'black',height:'100px',textAlign:'center'}}>
                <p style={{fontSize:'60px',fontFamily:'Times new roman',color:'white'}}>Sign Up</p>
            </div>
            <br/>
            <br/>
            <p style={{fontSize:'50px',fontFamily:'Times new roman',color:'navy'}}> UserName: </p>
            <input style={{fontFamily:'Times new roman',color:'green',width:'300px',height:'45px',fontSize:'30px'}} type='text' placeholder='Enter your userName' onChange={this.handleuserName} />
            <br/>
            <br/>
            {vailduserName}
            <br/>
            <br/>
            <p style={{fontSize:'50px',fontFamily:'Times new roman',color:'navy'}}> Password: </p>
            <input style={{fontFamily:'Times new roman',color:'green',width:'300px',height:'45px',fontSize:'30px'}} type='password' placeholder='Enter your Password' onChange={this.handleuserPassword} />
            <br/>
            <br/>
            {vailduserPass}
            <br/>
            <br/>
            <br/>
            <Link to={path}><button style={{width:'250px',height:'60px',fontSize:'30px'}} type='button' className='btn btn-info' onClick={this.vailedUser} disabled={(!this.state.vailduserName && !this.state.vailduserPass) ? false :true}> Submit </button></Link>             
            <br/>
        </div>
        )
    }
}


