import React, { Component } from 'react'
import {Link} from 'react-router-dom';

export default class Help extends Component {
    
    constructor(props) {
        super(props)
    
        this.state = {
             user:props.user,
             service:props.service,
             show:false,
             trueCount:0,
             faleCount:0,
             pass:'',
        }
    }

    show=()=>{
        this.setState({show:true})
    }

    pass=(e)=>{
        this.setState({pass:e.target.value})
    }

    cancle=()=>{
        if(this.state.pass === this.state.user.userPass){
            this.setState({trueCount:this.state.trueCount+1})
        }else{
            this.setState({faleCount:this.state.faleCount+1})
        }
        this.setState({pass:''})
    }
    
    render() {
        let button = <button onClick={this.show} style={{width:'250px',height:'60px',fontSize:'25px',position:'relative',right:'20px'}} type='button' className='btn btn-danger' > Cancle </button>
        let show = '';
        let path=`/main/${this.state.user.userName}`
        if(this.state.show){
            show = <input onChange={this.pass} style={{fontFamily:'Times new roman',color:'red',width:'300px',height:'60px',fontSize:'30px'}} value={this.state.pass} placeholder="password" type='password' />
            button = <button onClick={this.cancle} style={{width:'250px',height:'60px',fontSize:'25px',position:'relative',right:'20px',bottom:'5px'}} type='button' className='btn btn-danger'> Confirm </button>
        }
        if(this.state.faleCount === 3){
            show = ""
            button = <p style={{fontSize:'40px',fontFamily:'Times new roman',color:'red'}}> Your passwoed dosen't match , cant cancle this reguest </p>
        }
        if(this.state.trueCount === 2){
            button = <Link to ={path}> <button style={{width:'250px',height:'60px',fontSize:'25px',position:'relative',right:'20px'}} type='button' className='btn btn-danger' > Cancle </button> </Link>
            show=''
        }
        return (
            <div style={{textAlign:'center'}}>
                 <div style={{backgroundColor:'black',height:'100px',textAlign:'center'}}>
                     <p style={{fontSize:'60px',fontFamily:'Times new roman',color:'white'}}>{this.state.service}</p>
                </div>
                <br/>
                <br/>
                <br/>
                <div style={{textAlign:'center',width:'600px',height:'300px',borderStyle:'groove',position:'relative',left:'1000px'}}>
                    <p style={{fontSize:'60px',fontFamily:'Times new roman',color:'navy'}}>{this.state.service}</p>
                    <p style={{fontSize:'60px',fontFamily:'Times new roman',color:'navy'}}>{this.state.user.userName}</p>
                </div>
                <br/>
                <br/>
                <br/>
                {button}
                {show}
            </div>
        )
    }
}