import React, { Component } from 'react'
import Navar from './Navar'
import {Link} from 'react-router-dom';

export default class Main extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             service:props.service,
             user:props.user
        }
    }
    
    render() {
    let path1 = `/help/${this.state.service}`
        return (
            <div className='App'>
                <Navar chooseService={this.props.chooseService} service={this.state.service}/>
                <br/>
                <br/>
                <br/>
                <Link to ={path1} ><button type='button' className='btn btn-danger' style={{width:'500px',height:'500px',fontSize:'120px',borderRadius:'500px'}}> Help! </button></Link>
            </div>
        )
    }
}
