import React from 'react'

export default function Navar(props) {

    let  chooseService =(e)=>{
        props.chooseService(e.target.value)
    }
    
    return (
        <div style={{backgroundColor:'black',height:'100px',textAlign:'center',fontSize:'35px',color:'white',position:'relative'}}>
            <span>
            <p style={{position:'relative',top:'20px',fontSize:'35px',marginRight:'800px'}}> {props.service} </p>
                <select onChange={chooseService} style={{position:'relative',bottom:'45px',textAlign:'center',left:'500px'}}>
                    <option value>â˜°</option>
                    <option value='100 - Police'>100</option>
                    <option value='101 - Ambulance'>101</option>
                    <option value='102 - Firefighting'>102</option>
                </select>
            </span>
        </div>
    )
}